.cfooter
  margin-top: 80px
  background-color: $dark-color
  padding: 45px 0
  .ccontainer
    display: flex
    align-items: flex-start
    justify-content: flex-start
    flex-wrap: wrap
  &__top
    display: flex
    align-items: flex-start
    justify-content: flex-start
    flex-wrap: wrap
    &__column
      flex: 0 0 calc(33.3% - 20px)
      max-width: calc(33.3% - 20px)
      margin: 0 30px 30px 0
      &:nth-of-type(3n)
        margin-right: 0
      .menu-item
        margin-top: 30px
        &:nth-of-type(1)
          margin-top: 0
        .menu-title
          font-family: $default-font
          font-weight: 700
          font-size: 14px
          line-height: 20px
          color: $pink-color
          letter-spacing: .03em
          text-transform: uppercase
        ul
          margin: 8px 0 0
          padding: 0
          list-style-type: none
          li
            display: block
            margin-top: 11px
            &:nth-of-type(1)
              margin-top: 0
            a
              color: #fff
              text-decoration: none
              font-family: $default-font
              font-size: 14px
              line-height: 20px
              letter-spacing: .03em
              &:hover
                text-decoration: underline
        &.main-item
          ul
            li
              margin-top: 8px
              &:nth-of-type(1)
                margin-top: 0
              a
                font-family: $default-font
                font-weight: 700
                font-size: 14px
                line-height: 20px
                color: $pink-color
                letter-spacing: .03em
                text-transform: uppercase
                position: relative
                transition: color .3s
                &:before
                  content: ''
                  display: block
                  width: 0
                  height: 1px
                  background-color: $pink-color
                  position: absolute
                  bottom: 0
                  left: 0
                  transition: width .3s, background-color .3s
                &:hover
                  color: #fff
                  text-decoration: none
                  &:before
                    width: 100%
                    background-color: #fff
  &__bottom
    margin-top: 8px
    padding: 38px 0
    border-top: 1px solid rgba(255, 255, 255, 0.2)
    &__column
      flex: 0 0 calc(33.3% - 20px)
      max-width: calc(33.3% - 20px)
      margin: 0 30px 60px 0
      &:nth-of-type(3n)
        margin-right: 0
      .social-links
        display: flex
        align-items: center
        justify-content: flex-start
        margin: 0
        padding: 15px 0
        list-style-type: none
        &__item
          flex: 0 0 auto
          width: auto
          margin: 0 57px 0 0
          &:last-child
            margin-right: 0
        &__link
          color: #fff
          font-size: 21px
          line-height: 25px
          text-decoration: none
          transition: color .3s
          .icon.icon-youtube-play
            font-size: 20px
          &:hover
            color: $pink-color
      .backcall-btn
        border-color: $pink-color
        display: block
        width: 100%
        max-width: 255px
        margin: 0
        color: #fff
        font-weight: 700
        font-size: 16px
        line-height: 16px
        padding: 15px 0
      .apps-links
        display: flex
        align-items: center
        justify-content: flex-start
        flex-wrap: wrap
        padding: 10px 0
        &__item
          flex: 0 0 auto
          width: auto
          &:nth-of-type(1)
            margin-right: 20px
        &__link
          display: block
          transition: transform .3s
          &:hover
            transform: translateY(-5px)
          .app-image
            display: block
            width: auto
            max-width: 100%
    .copyright
      display: inline-block
      flex: 0 0 auto
      width: auto
      font-family: $default-font
      font-size: 12px
      line-height: 12px
      letter-spacing: .01em
      color: #fff
    .payment-list
      flex: 0 0 auto
      width: auto
      display: flex
      align-items: center
      justify-content: center
      margin: 0 auto
      &__item
        flex: 0 0 auto
        width: auto
        margin: 0 7px
        .payment-image
          display: block
          width: auto
          max-width: 100%
    .info-links
      flex: 0 0 auto
      width: auto
      display: flex
      align-items: center
      justify-content: flex-end
      &__item
        display: inline-block
        flex: 0 0 auto
        width: auto
        margin-left: 15px
        &:nth-of-type(1)
          margin-left: 0
      &__link
        font-family: $default-font
        font-size: 12px
        line-height: 14px
        color: #fff
        letter-spacing: .03em
        &:hover
          text-decoration: none
