.section-title {
  font-family: DroidSans, sans-serif;
  font-weight: 700;
  font-size: 32px;
  line-height: 40px;
  color: #1C1B1F;
  letter-spacing: 0.03em;
}

.input-style {
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid transparent;
  color: #000;
  padding: 13px 18px;
  font-family: DroidSans, sans-serif;
  font-size: 16px;
  line-height: 16px;
}
.input-style.placeholder {
  color: #747F87;
}
.input-style::-webkit-input-placeholder {
  color: #747F87;
}
.input-style::-moz-placeholder {
  color: #747F87;
}
.input-style:-moz-placeholder {
  color: #747F87;
}
.input-style:-ms-input-placeholder {
  color: #747F87;
}
.input-style.error {
  border-color: red;
}

.green-btn {
  display: block;
  width: 100%;
  background-color: #6FDE9B;
  color: #fff;
  border-radius: 70px;
  font-family: DroidSans, sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  padding: 16px 0;
  letter-spacing: 0.03em;
  transition: background-color 0.3s;
}

.stroke-btn {
  display: inline-block;
  border: 2px solid #000;
  color: #000;
  border-radius: 100px;
  font-family: DroidSans, sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  padding: 13px 22px;
  letter-spacing: 0.03em;
  transition: background-color 0.3s, color 0.3s, border-color 0.3s;
}
.stroke-btn:hover {
  color: #fff;
  border-color: #F96791;
  background-color: #F96791;
}
.stroke-btn.pink-style {
  border-color: #F96791;
  color: #F96791;
}
.stroke-btn.pink-style:hover {
  color: #fff;
}

.check-group label {
  display: inline-block;
  position: relative;
  font-family: DroidSans, sans-serif;
  font-size: 11px;
  line-height: 15px;
  letter-spacing: 0.025em;
  color: #fff;
  cursor: pointer;
}
.check-group label span {
  display: block;
  position: relative;
  padding-left: 44px;
}
.check-group label span:before {
  content: "\e800";
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  position: absolute;
  top: 0;
  left: 3px;
  width: 14px;
  height: 14px;
  border: 2px solid #fff;
  border-radius: 2px;
  color: transparent;
  transition: color 0.3s;
}
.check-group label input {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  visibility: hidden;
  opacity: 0;
}
.check-group label input:checked + span:before {
  color: #fff;
}
.check-group label a {
  color: #fff;
}
.check-group label a:hover {
  text-decoration: none;
}

.custom-select .jq-selectbox__select {
  background-color: #fff;
  position: relative;
}
.custom-select .jq-selectbox__select .jq-selectbox__select-text {
  padding: 20px 25px 20px 10px;
}
.custom-select .jq-selectbox__select .jq-selectbox__trigger {
  position: absolute;
  top: 0;
  right: 0;
  width: 35px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.custom-select .jq-selectbox__select .jq-selectbox__trigger .jq-selectbox__trigger-arrow:after {
  content: "\e806";
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: block;
  margin: 0;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
}
.custom-select .jq-selectbox__dropdown {
  width: 100%;
  top: 100%;
  left: 0;
  background-color: #fff;
  border: 1px solid #eee;
}
.custom-select .jq-selectbox__dropdown ul::-webkit-scrollbar {
  width: 2px;
  height: 2px;
  border-radius: 0;
}
.custom-select .jq-selectbox__dropdown ul::-webkit-scrollbar-thumb {
  background: #F96791;
  border-radius: 0;
  transition: background-color 0.3s;
}
.custom-select .jq-selectbox__dropdown ul::-webkit-scrollbar-thumb:hover {
  background: #6FDE9B;
}
.custom-select .jq-selectbox__dropdown ul::-webkit-scrollbar-track {
  background: #F3F5F6;
  border-radius: 0;
}
.custom-select .jq-selectbox__dropdown ul {
  scrollbar-face-color: #F96791;
  scrollbar-track-color: #F3F5F6;
}
.custom-select .jq-selectbox__dropdown ul li {
  display: block;
  padding: 10px 10px;
  font-size: 14px;
  line-height: 16px;
  border-top: 1px solid #eee;
  background-color: #fff;
  transition: background-color 0.3s;
}
.custom-select .jq-selectbox__dropdown ul li:hover {
  background-color: #eee;
}
.custom-select .jq-selectbox__dropdown ul li:nth-of-type(1) {
  border-top: none;
}

