@import "settings"
@import "_mixins/_mixins"
@import "_components/ui"

/* main page */
.cstart-form
  margin-top: 105px
  .ccontainer
    max-width: 1300px
    background-color: $pink-color
    border-radius: 30px
    padding: 0
    position: relative
    @media (max-width: 1200px)
      max-width: calc(100% - 30px)
    &:before
      content: url("../images/common/main/girl1.png")
      position: absolute
      bottom: calc(100% - 59px)
      left: calc(50% - 192px)
      @media (max-width: 1200px)
        transform: scale(0.75)
        bottom: calc(100% - 35px)
        left: calc(50% - 165px)
    &:after
      content: url("../images/common/svg/green_arrow.svg")
      position: absolute
      bottom: calc(100% - 30px)
      left: calc(55% - 12px)
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    margin: 0 auto
    overflow: hidden
    &__column
      flex: 0 0 auto
      max-width: 30%
      padding: 62px 0 57px
      @media (max-width: 1200px)
        padding: 30px 0 30px
      &:nth-of-type(1)
        flex: 0 0 40%
        max-width: 40%
        padding-left: 97px
        @media (max-width: 1200px)
          padding-left: 40px
      &:nth-of-type(2)
        margin-left: 3.75%
        @media (max-width: 1200px)
          margin-left: 1.25%
      &:nth-of-type(3)
        flex: 0 0 auto
        width: auto
        display: flex
        flex-direction: column
        align-items: center
        justify-content: center
        padding: 0 97px 0 30px
        position: relative
        @media (max-width: 1200px)
          padding: 0 30px
        &:after
          content: url("../images/common/svg/radial.svg")
          position: absolute
          width: auto
          height: auto
          top: 52%
          left: 25%
          margin-right: -50%
          transform: translate(-50%, -50%)
  &__title
    font-family: $default-font
    font-weight: 700
    font-size: 31px
    line-height: 36px
    letter-spacing: .02em
    color: #fff
  &__subtitle
    display: inline-block
    color: #fff
    font-family: $default-font
    font-weight: 700
    font-size: 20px
    line-height: 29px
    letter-spacing: .03em
    text-align: center
    margin-bottom: 40px
    position: relative
    z-index: 1
    &:before
      content: url("../images/common/svg/star1.svg")
      display: block
      position: absolute
      bottom: calc(100% + 10px)
      left: 27px
    &:after
      content: url("../images/common/svg/star2.svg")
      display: block
      position: absolute
      top: calc(100% + 26px)
      right: 8px
    span
      text-transform: uppercase
  &__list
    margin-top: 44px
    ul
      margin: 0
      padding: 0
      list-style-type: none
      color: #fff
      font-family: $default-font
      font-size: 16px
      line-height: 21px
      letter-spacing: .02em
      li
        display: block
        position: relative
        padding-left: 32px
        margin-top: 21px
        &:nth-of-type(1)
          margin-top: 0
        &:before
          content: '\e801'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          color: #fff
          position: absolute
          top: 0
          left: -2px
          font-size: 18px
          line-height: 18px
  &__form
    max-width: 350px
    position: relative
    z-index: 1
    .input-group
      display: block
      width: 100%
      margin-top: 26px
      position: relative
      &:nth-of-type(1)
        margin-top: 0
      &.check-group
        margin-top: 14px
      input
        width: 100%
      .input-message
        display: block
        position: absolute
        top: calc(100% + 2px)
        left: 0
        color: #000
        font-family: $default-font
        font-size: 12px
        line-height: 12px
        letter-spacing: .025em
        margin: 0
        .icon
          display: inline-block
          font-size: 16px
          line-height: 16px
          margin-right: 13px
          position: relative
          &:after
            content: ''
            display: block
            width: 90%
            height: 90%
            background-color: #fff
            position: absolute
            top: 5%
            left: 5%
            border-radius: 50%
            z-index: -15
.cplatform-block
  margin-top: 83px
  &__title
    font-size: 36px
    line-height: 41px
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    background: $gradient
    margin-top: 40px
    border-radius: 30px
    padding: 40px 90px
    &__left-side
      flex: 0 0 50%
      max-width: 50%
      h3
        font-family: $default-font
        font-size: 24px
        line-height: 30px
        text-transform: uppercase
        font-weight: 400
        letter-spacing: .04em
      ul
        margin: 41px 0 0
        padding: 0
        list-style-type: none
        font-family: $default-font
        font-size: 16px
        line-height: 24px
        letter-spacing: .03em
        max-width: 355px
        li
          display: block
          position: relative
          margin-top: 24px
          &:nth-of-type(1)
            margin-top: 0
          &:before
            content: '\e801'
            font-family: Fontello
            font-style: normal
            font-weight: normal
            speak: never
            display: block
            margin: 0
            text-decoration: inherit
            text-align: center
            font-variant: normal
            text-transform: none
            color: #fff
            position: absolute
            top: 2px
            left: -47px
            font-size: 18px
            line-height: 18px
      .buy-btn
        margin: 20px 0 0 5px
    &__right-side
      flex: 0 0 50%
      max-width: 50%
      display: flex
      flex-direction: column
      align-items: center
      justify-content: center
      padding-top: 60px
      h3
        padding-left: 15px
        font-family: $default-font
        font-weight: 700
        font-size: 20px
        line-height: 23px
        color: #000
        letter-spacing: .02em
      ul
        display: flex
        align-items: center
        justify-content: center
        flex-wrap: wrap
        margin: 35px auto 0
        padding: 0
        list-style-type: none
        li
          display: inline-block
          border: 2px solid #fff
          text-align: center
          padding: 19px 13px
          text-transform: uppercase
          font-family: $default-font
          font-size: 16px
          line-height: 20px
          text-transform: uppercase
          border-radius: 20px
          letter-spacing: .03em
          color: #fff
          margin: 0 9px 18px 9px
          &:nth-of-type(1)
            margin: 0 18px 18px

.ctraining-types
  margin-top: 80px
  &__title
    font-family: $default-font
    font-weight: 700
    font-size: 20px
    line-height: 23px
    letter-spacing: .03em
    color: #000
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    margin-top: 40px
    &__item
      flex: 0 0 calc(33.3% - 20px)
      max-width: calc(33.3% - 20px)
      margin: 0 30px 0 0
      border-radius: 30px
      padding: 40px 33px
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: flex-start
      min-height: 313px
      &:nth-of-type(3n)
        margin-right: 0
      &:nth-of-type(1n)
        background-color: rgba($green-color, .5)
      &:nth-of-type(2n)
        background-color: rgba($blue-color, .5)
      &:nth-of-type(3n)
        background-color: rgba($pink-color, .5)
      .item-title
        font-family: $default-font
        font-size: 24px
        line-height: 30px
        font-weight: 400
        text-transform: uppercase
        letter-spacing: .035em
      .item-descr
        font-family: $default-font
        font-size: 16px
        line-height: 25px
        margin-top: 10px
        letter-spacing: .03em
        margin-bottom: 20px
      .item-btn
        margin-top: auto
        padding: 12px 30px
        min-width: 187px
.ccurses-block
  margin-top: 80px
  position: relative
  &:before
    content: ''
    display: block
    width: 100%
    height: calc(100% - 42% + 4px)
    position: absolute
    top: calc(42% - 4px)
    left: 0
    background-color: $gray-darkness-color
  .ccontainer
    background-color: $gray-color
    border-radius: 40px
    max-width: 1110px
    padding: 32px 32px 80px
    position: relative
    z-index: 1
  &__title
    margin-top: 48px
    line-height: 38px
    letter-spacing: .025em
    max-width: 730px
  &__choose
    margin-top: 40px
    h3
      font-family: $default-font
      font-weight: 700
      font-size: 16px
      line-height: 20px
      color: #000000
      letter-spacing: .02em
    .choose-select
      font-family: $default-font
      font-weight: 700
      font-size: 12px
      line-height: 12px
      margin-top: 15px
      display: block
      width: 100%
      max-width: 256px
      cursor: pointer
  &__tabs
    margin-top: 40px
    &__item
      display: none
      align-items: stretch
      justify-content: flex-start
      flex-wrap: wrap
      &.active
        display: flex
      .tab-title
        font-family: $default-font
        font-weight: 700
        font-size: 20px
        line-height: 23px
        color: #000
        letter-spacing: .025em
      .tab-descr
        margin-top: 35px
        padding-left: 15px
        ul
          margin: 0
          padding: 0
          list-style-type: none
          font-family: $default-font
          font-size: 16px
          line-height: 24px
          letter-spacing: .02em
          li
            display: block
            position: relative
            padding-left: 48px
            margin-top: 24px
            &:nth-of-type(1)
              margin-top: 0
            &:before
              content: '\e801'
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              display: block
              margin: 0
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              font-size: 18px
              line-height: 18px
              position: absolute
              top: 1px
              left: 0
              color: $pink-color
    .tab
      &__left-side
        flex: 0 0 50%
        max-width: 50%
        padding-right: 45px
        @media (max-width: 1200px)
          padding-right: 15px
      &__right-side
        flex: 0 0 50%
        max-width: 50%
        padding-left: 15px

  &__btn-row
    width: 100%
    text-align: center
    margin-top: 55px
    .all-curses
      display: inline-block
      font-family: $default-font
      font-weight: 700
      font-size: 14px
      line-height: 18px
      letter-spacing: .03em
      color: $txt-color
      transition: color .3s
      &:hover
        color: $pink-color
        text-decoration: none
.ccards-block
  background-color: $gray-darkness-color
  padding: 80px 0
  .title-row
    display: flex
    align-items: flex-end
    justify-content: flex-end
    flex-wrap: wrap
  &__title
    display: inline-block
    margin-right: auto
    line-height: 37px
  &__subtitle
    font-family: Veles, sans-serif
    font-weight: 700
    font-size: 30px
    line-height: 30px
    color: $pink-color
  &__content
    .ccards-slider
      margin-top: 40px
      .swiper-wrapper
        margin-left: -80px
    .slide-item
        max-width: 920px
        width: 100%
        margin-right: 30px
        &__content
          display: flex
          align-items: stretch
          justify-content: flex-start
          border-radius: 30px
          background-color: #fff
          overflow: hidden
          &__left-side
            flex: 0 0 62%
            max-width: 62%
            padding: 42px 65px 35px
            .cards-title
              font-family: $default-font
              color: #000
              font-weight: 400
              font-size: 24px
              line-height: 30px
              text-transform: uppercase
              letter-spacing: .03em
            .cards-level
              display: inline-block
              margin-top: 21px
              font-family: $default-font
              font-weight: 700
              font-size: 12px
              line-height: 20px
              letter-spacing: .02em
              color: $blue-color
            .cards-descr
              margin-top: 35px
              ul
                margin: 0
                padding: 0
                list-style-type: none
                font-family: $default-font
                font-size: 16px
                line-height: 24px
                color: $txt-color
                letter-spacing: .025em
                li
                  display: block
                  position: relative
                  padding-left: 30px
                  margin-top: 24px
                  &:nth-of-type(1)
                    margin-top: 0
                  &:before
                    content: ''
                    display: block
                    width: 6px
                    height: 6px
                    border-radius: 50%
                    background-color: $txt-color
                    position: absolute
                    top: 7px
                    left: 0
            .more-link
              margin-top: 37px
              padding: 13px 25px

          &__right-side
            flex: 0 0 38%
            max-width: 38%
            position: relative
            .tags-list
              display: flex
              flex-direction: column
              align-items: flex-start
              justify-content: flex-start
              position: absolute
              top: 42px
              right: 0
              &__item
                flex: 0 0 auto
                display: inline-block
                border-radius: 17px 0 0 17px
                text-transform: uppercase
                font-family: $default-font
                font-weight: 700
                font-size: 12px
                line-height: 20px
                letter-spacing: .03em
                padding: 5px 15px 5px 20px
                &.sale-item
                  color: #fff
                  background-color: $green-color
            .slide-image
              display: block
              width: auto
              max-width: 100%
              object-fit: cover
  .navigation-row
    display: flex
    align-items: center
    justify-content: center
    padding: 0 100px
    position: relative
    margin-top: 42px
    .slider-btns
      position: absolute
      top: 0
      left: 0
      .slider-btn
        display: inline-block
        font-size: 28px
        line-height: 28px
        color: #000
        transition: color .3s
        cursor: pointer
        &:hover
          color: $pink-color
        &.swiper-button-disabled
          opacity: .5
          &:hover
            color: #000
        &.prev-slide
          transform: scale(-1,1)
        &.next-slide
          margin-left: 32px
    .more-cards
      display: inline-block
      flex: 0 0 auto
      width: auto
      font-family: $default-font
      font-weight: 700
      font-size: 14px
      line-height: 28px
      letter-spacing: .03em
      color: $txt-color
      transition: color .3s
      &:hover
        color: $pink-color
        text-decoration: none

.cpayment-block
  margin-top: 80px
  &__content
    display: flex
    align-items: flex-start // center
    justify-content: flex-start
    &__left-side
      display: flex
      align-items: center
      justify-content: flex-start
      flex: 0 0 calc(50% + 15px)
      max-width: calc(50% + 15px)
    &__right-side
      display: flex
      align-items: stretch
      justify-content: flex-start
      flex: 0 0 calc(50% - 15px)
      max-width: calc(50% - 15px)
      padding-top: 60px
      .payment-item
        flex: 0 0 calc(33.3% - 20px)
        max-width: calc(33.3% - 20px)
        background-color: #F7F8F9
        border-radius: 30px
        margin: 0 30px 30px 0
        text-align: center
        padding: 34px 0
        &:nth-of-type(3)
          margin-right: 0
        h3
          display: inline-block
          font-family: $default-font
          font-weight: 700
          font-size: 14px
          line-height: 19px
          color: $txt-color
          span
            display: block
            width: 100%
            color: $pink-color
            text-align: center
            &:nth-of-type(1)
              font-size: 91px
              line-height: 91px
            &:nth-of-type(2)
              font-size: 21px
              line-height: 12px
        &:nth-of-type(3)
          h3
            span
              &:nth-of-type(1)
                font-size: 69px
                line-height: 92px
  &__title
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    font-family: $default-font
    font-weight: 700
    font-size: 18px
    line-height: 24px
    color: $pink-color
    text-align: center
    margin-top: 55px
    span
      display: inline-block
      font-family: Veles, sans-serif
      font-weight: 700
      font-size: 58px
      line-height: 77px
      margin-top: -10px
      padding: 0 15px 40px
      background: linear-gradient(75.57deg, rgba(#FA6173,.5) 15.62%, rgba(#1693DC,.5) 57.16%, rgba(#00C753,.5) 94.08%)
      -webkit-background-clip: text
      -webkit-text-fill-color: transparent
      background-clip: text
      text-fill-color: transparent
      order: 1
  &__image
    display: block
    width: auto
    max-width: 100%
    margin: 5px 0 0 -5px

.canswers-block
  margin-top: 48px
  .title-row
    display: flex
    align-items: flex-start
    justify-content: flex-start
    .navigation-row
      display: flex
      align-items: center
      justify-content: center
      position: relative
      margin: 8px 3px 0 auto
      .slider-btn
        display: inline-block
        font-size: 28px
        line-height: 28px
        color: $pink-color
        transition: color .3s
        cursor: pointer
        &:hover
          color: $pink-darkness-color
        &.swiper-button-disabled
          color: #000
          opacity: .5
          &:hover
            color: #000
        &.prev-slide
          transform: scale(-1,1)
        &.next-slide
          margin-left: 32px
  &__title
    .emodji
      display: inline-block
      position: relative
      top: 5px
      left: -5px
  &__content
    margin-top: 24px
    .canswers-slider
      padding: 45px 20px
      width: calc(100% + 40px)
      max-width: calc(100% + 40px)
      margin: 0 -20px
      .slide-item
        background-color: #FBFBFB
        box-shadow: 0 0 15px -5px rgb(0 0 0 / 12%)
        border-radius: 30px
        max-width: 256px
        overflow: hidden
        .slide-link
          display: block
        .slide-image
          display: block
          width: auto
          max-width: 100%
          margin: 0
.chelp-form
  margin-top: 105px
  .ccontainer
    max-width: 1250px
    background-color: $pink-color
    border-radius: 30px
    padding: 80px 69px 33px
    @media (max-width: 1200px)
      padding: 50px 50px 35px
      max-width: calc(100% - 30px)
  &__title
    color: #fff
    padding-left: calc(25% + 10px)
    line-height: 37px
    letter-spacing: .02em
  &__form
    display: flex
    align-items: center
    justify-content: flex-start
    flex-wrap: wrap
    margin-top: 45px
    .input-group
      flex: 0 0 calc(25% - 22px)
      max-width: calc(25% - 22px)
      margin: 0 28px 15px 0
      &:nth-of-type(4)
        margin-right: 0
      &.check-group
        flex: 0 0 50%
        max-width: 50%
        @media (max-width: 1200px)
          flex: 0 0 55%
          max-width: 55%
      input
        width: 100%
  .title-row
    display: flex
    align-items: flex-end
    justify-content: flex-end
    position: relative
    .help-image
      display: block
      position: absolute
      left: 0
      bottom: calc(100% - 67px)
      width: auto
      max-width: 100%
      margin: 0
@import "_components/footer"
@import "node_modules/intl-tel-input/build/css/intlTelInput"
.iti
  width: 100%
.iti__flag
  background-image: url("../images/flags/flags.png")
  transform: scale(1.4)
  border: 0.5px solid #eee
  border-radius: 3px
  overflow: hidden
  box-shadow: none
.iti--separate-dial-code .iti__selected-flag
  background-color: transparent
.iti__selected-flag
  padding: 0 6px 0 20px
.iti--separate-dial-code .iti__selected-dial-code
  color: $txt-color
  font-family: $default-font
  font-size: 20px
  line-height: 20px
  margin-left: 10px
.iti__arrow
  margin-left: 8px
  width: 0
  height: 0
  border-left: 6px solid transparent
  border-right: 6px solid transparent
  border-top: 6px solid $txt-color
.iti__arrow--up
  border-top: none
  border-bottom: 6px solid $txt-color

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi)
  .iti__flag
    background-image: url("../images/flags/flags@2x.png")
@import "_components/modals"
@import "_animates/_animates"