@import "../settings"

.section-title
  font-family: $default-font
  font-weight: 700
  font-size: 32px
  line-height: 40px
  color: #1C1B1F
  letter-spacing: .03em
.input-style
  background-color: #fff
  border-radius: $input-border-radius
  border: $input-border
  color: $input-color
  padding: $input-padding
  font-family: $default-font
  font-size: 16px
  line-height: 16px
  @include input-placeholder
    color: $input-placeholder-color
  &.error
    border-color: $input-error-border-color
.green-btn
  display: block
  width: 100%
  background-color: $green-color
  color: #fff
  border-radius: 70px
  font-family: $default-font
  font-weight: 700
  font-size: 16px
  line-height: 16px
  text-transform: uppercase
  text-align: center
  cursor: pointer
  padding: 16px 0
  letter-spacing: .03em
  transition: background-color .3s
.stroke-btn
  display: inline-block
  border: 2px solid #000
  color: #000
  border-radius: 100px
  font-family: $default-font
  font-weight: 700
  font-size: 12px
  line-height: 20px
  text-transform: uppercase
  text-decoration: none
  text-align: center
  padding: 13px 22px
  letter-spacing: .03em
  transition: background-color .3s, color .3s, border-color .3s
  &:hover
    color: #fff
    border-color: $pink-color
    background-color: $pink-color
  &.pink-style
    border-color: $pink-color
    color: $pink-color
    &:hover
      color: #fff
.check-group
  label
    display: inline-block
    position: relative
    font-family: $default-font
    font-size: 11px
    line-height: 15px
    letter-spacing: .025em
    color: #fff
    cursor: pointer
    span
      display: block
      position: relative
      padding-left: 44px
      &:before
        content: '\e800'
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        display: flex
        align-items: center
        justify-content: center
        margin: 0
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
        position: absolute
        top: 0
        left: 3px
        width: 14px
        height: 14px
        border: 2px solid #fff
        border-radius: 2px
        color: transparent
        transition: color .3s
    input
      position: absolute
      top: 0
      left: 0
      width: 0
      height: 0
      visibility: hidden
      opacity: 0
      &:checked + span:before
        color: #fff
    a
      color: #fff
      &:hover
        text-decoration: none

.custom-select
  .jq-selectbox__select
    background-color: #fff
    position: relative
    .jq-selectbox__select-text
      padding: 20px 25px 20px 10px
    .jq-selectbox__trigger
      position: absolute
      top: 0
      right: 0
      width: 35px
      height: 100%
      display: flex
      align-items: center
      justify-content: center
      .jq-selectbox__trigger-arrow:after
        content: '\e806'
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        display: block
        margin: 0
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
  .jq-selectbox__dropdown
    width: 100%
    top: 100%
    left: 0
    background-color: #fff
    border: 1px solid #eee
    ul
      @include scrollbars(2px, $scrl-bar_track, $scrl-bar_thumb, $scrl-bar_thumbh, 0)
      li
        display: block
        padding: 10px 10px
        font-size: 14px
        line-height: 16px
        border-top: 1px solid #eee
        background-color: #fff
        transition: background-color .3s
        &:hover
          background-color: #eee
        &:nth-of-type(1)
          border-top: none